/* 2.- CONTENT
   ----------------------------- */
html, body {
  height: 100%;
  margin: 0;
  overflow: hidden;
  padding: 0; }

.wrapper-out {
  height: 100%;
  position: fixed;
  width: 100%; }

.wrapper-in {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  height: 100%;
  position: relative;
  width: 100%; }

.main-container {
  width: calc(100% - 498px); }

/* 3.- CONTROLS
   ----------------------------- */
.disabled {
  opacity: 0.2; }

/* 3.1.- CONTROLS: Button
   ----------------------------- */
input[type="text"]:focus {
  outline: 0; }

/* 3.2.- CONTROLS: Input text
   ----------------------------- */
button {
  border: none; }
  button:focus {
    outline: 0; }

.button-a {
  border-radius: 100px;
  color: white;
  height: 31px;
  text-transform: uppercase; }
  .button-a i {
    font-size: 18px;
    position: relative;
    top: 2px; }
  .button-a span {
    font-family: NunitoSans;
    font-size: 13px;
    font-weight: 700;
    position: relative;
    top: -1px; }



