.checkmark-container {
  -ms-flex-align: center;
      align-items: center;
  cursor: pointer;
  display: -ms-flexbox;
  display: flex;
  position: relative; }

.checkmark {
  height: 10px;
  width: 10px;
  border-radius: 3px;
  border: solid 2px #e3e3e3;
  margin-left: 0px; }
  .checkmark:after {
    content: "";
    position: absolute;
    display: none; }

.checkmark-checked {
  background-color: #2196F3;
  border-color: #2196F3; }
  .checkmark-checked:after {
    display: block;
    left: 5px;
    top: 3px;
    width: 3px;
    height: 9px;
    border: solid white;
    border-width: 0 2px 2px 0;
    border-radius: 2px;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg); }

.checkmark-partial {
  background-color: #2196F3;
  border: none;
  height: 14px;
  width: 14px; }
  .checkmark-partial:after {
    display: block;
    left: 3px;
    top: -3px;
    width: 8px;
    height: 9px;
    border: solid white;
    border-width: 0 0px 2px 0; }

.checkmark-text {
  color: black;
  font-size: 13px;
  font-weight: 200;
  margin-left: 12px; }



